import React from 'react';
import { api } from './api/api';

export default class ErrorBoundary extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      error: null,
      requesting: false,
      requested: false,
    };
  }

  componentDidCatch(error, info) {
    this.setState({ error: { error, info }, requesting: true });
    // logErrorToMyService(error, info);
    console.error('catched error', error);
    console.error('catched info', info);
    api.request('error_report', 'POST', { error: { error, info } }).then(resp => {
      this.setState({ requested: true });
    });
  }

  render() {
    if (this.state.error) {
      const content = (this.state.requesting && !this.state.requested) ?
        <div style={{ color: 'orange' }}>...</div> :
        <div style={{ color: 'green' }}><div>
          {window.location.hash === '#/' ? null : <div><br /><a href="/"></a></div>}
        </div>
        </div>;
      return <div>
        <b><h3></h3></b>
        <p></p>
        <hr />
        {content}
        <p></p>
        <p><code>zhicheng1840</code> / <a href="mailto:chiro2001@163.com">chiro2001@163.com</a> / chiro2001</p>
        <hr></hr>
        <p></p>
        <code>{JSON.stringify(this.state.error)}</code>
      </div>;
    }
    return this.props.children;
  }
}